import { gsap } from 'gsap'
import { preloader } from './assists/preloader'
import { map, lerp, clamp, getMousePos } from './assists/utils'

// import images
const images = importAll(require.context('../images/tech-images', false, /\.(png|jpe?g|svg)$/))
function importAll(r) {
  return r.keys().map(r)
}

export function techsAnimation() {
  const menuEl = document.querySelector('.techs-menu')

  // preload the images set as data attrs in the menu items
  preloader('.tech-item').then(() => {
    new Menu(menuEl)
  })
}

//.............MENU.................

function Menu(el) {
  let DOM = { el: el }
  // the menu item elements (<a>)
  DOM.menuItems = DOM.el.querySelectorAll('.tech-item')
  // menu item properties that will animate as we move the mouse around the menu
  // we will be using interpolation to achieve smooth animations.
  // the “previous” and “current” values are the values to interpolate.
  // the value applied to the element, this case the image element (let DOM.reveal) will be a value between these two values at a specific increment.
  // the amt is the amount to interpolate.
  let animatableProperties = {
    // translationX
    tx: { previous: 0, current: 0, amt: 0.08 },
    // translationY
    ty: { previous: 0, current: 0, amt: 0.08 },
    // Rotation angle
    rotation: { previous: 0, current: 0, amt: 0.04 },
    // CSS filter (brightness) value
    brightness: { previous: 1, current: 1, amt: 0.08 }
  }
  // array of MenuItem instances
  let menuItems = []
  // initialize the MenuItems
  Array.from(DOM.menuItems).forEach((item, pos) => {
    menuItems.push(new MenuItem(item, pos, animatableProperties))
  })
  // show the menu items (initial animation where each menu item gets revealed)
  gsap.to(
    menuItems.map((item) => item.DOM.textInner),
    {
      duration: 1.2,
      ease: 'Expo.easeOut',
      startAt: { y: '100%' },
      y: 0,
      delay: (pos) => pos * 0.06
    }
  )
}

//.................ITEM...........

// track the mouse position
let mousepos = { x: 0, y: 0 }
// cache the mouse position
let mousePosCache = mousepos
let direction = {
  x: mousePosCache.x - mousepos.x,
  y: mousePosCache.y - mousepos.y
}

// update mouse position when moving the mouse
window.addEventListener('mousemove', (ev) => (mousepos = getMousePos(ev)))

function MenuItem(el, inMenuPosition, animatableProperties) {
  // el is the <a> with class "tech-item"
  let DOM = { el: el }
  let firstRAFCycle = false
  let bounds = {}
  let tl
  let requestId
  // position in the Menu
  // menu item properties that will animate as we move the mouse around the menu
  // the item text
  DOM.textInner = DOM.el.querySelector('.tech-item-name-inner')

  // create the image structure
  DOM.reveal = document.createElement('div')
  DOM.reveal.className = 'hover-reveal'
  DOM.reveal.style.transformOrigin = '0% 0%'
  // the next two elements could actually be only one, the image element
  // adding an extra wrapper (revealInner) around the image element with overflow hidden, gives us the possibility to scale the image inside
  DOM.revealInner = document.createElement('div')
  DOM.revealInner.className = 'hover-reveal__inner'
  DOM.revealImage = document.createElement('div')
  DOM.revealImage.className = 'hover-reveal__img'
  DOM.revealImage.style.backgroundImage = `url(${images[inMenuPosition]})`

  DOM.revealInner.appendChild(DOM.revealImage)
  DOM.reveal.appendChild(DOM.revealInner)
  DOM.el.appendChild(DOM.reveal)
  // initialize some events
  const mouseenterFn = (ev) => {
    // show the image element
    showImage()
    firstRAFCycle = true
    // start the render loop animation (rAF)
    loopRender()
  }

  const mouseleaveFn = (ev) => {
    // stop the render loop animation (rAF)
    stopRendering()
    // hide the image element
    hideImage()
  }

  DOM.el.addEventListener('mouseenter', mouseenterFn)
  DOM.el.addEventListener('mouseleave', mouseleaveFn)
  // create the image structure
  // we want to add/append to the menu item the following html:
  // <div class="hover-reveal">
  //   <div class="hover-reveal__inner" style="overflow: hidden;">
  //     <div class="hover-reveal__img" style="background-image: url(pathToImage);">
  //     </div>
  //   </div>
  // </div>
  // calculate the position/size of both the menu item and reveal element
  function calcBounds() {
    bounds = {
      el: DOM.el.getBoundingClientRect(),
      reveal: DOM.reveal.getBoundingClientRect()
    }
  }

  // show the image element
  function showImage() {
    // kill any current tweens
    gsap.killTweensOf(DOM.revealInner)
    gsap.killTweensOf(DOM.revealImage)

    tl = gsap
      .timeline({
        onStart: () => {
          // show both image and its parent element
          DOM.reveal.style.opacity = DOM.revealInner.style.opacity = '1'
          // set a high z-index value so image appears on top of other elements
          gsap.set(DOM.el, { zIndex: 100 })
        }
      })
      .to(DOM.revealInner, {
        ease: 'Expo.easeOut',
        duration: 1.2,
        startAt: { scale: 0.3 },
        scale: 1
      })
      // animate the image element
      .to(
        DOM.revealImage,
        {
          ease: 'Expo.easeOut',
          duration: 1.2,
          startAt: { scale: 1.7 },
          scale: 1
        },
        0
      )
  }
  // hide the image element
  function hideImage() {
    // kill any current tweens
    gsap.killTweensOf(DOM.revealInner)
    gsap.killTweensOf(DOM.revealImage)

    tl = gsap
      .timeline({
        onStart: () => {
          gsap.set(DOM.el, { zIndex: 1 })
        },
        onComplete: () => {
          gsap.set(DOM.reveal, { opacity: 0 })
        }
      })
      .to(DOM.revealInner, {
        ease: 'Power3.easeOut',
        duration: 1.8,
        scale: 0.3,
        opacity: 0
      })
      .to(
        DOM.revealImage,
        {
          ease: 'Power3.easeOut',
          duration: 1.8,
          scale: 1.7
        },
        0
      )
  }
  // start the render loop animation (rAF)
  function loopRender() {
    if (!requestId) {
      requestId = requestAnimationFrame(() => render())
    }
  }
  // stop the render loop animation (rAF)
  function stopRendering() {
    if (requestId) {
      window.cancelAnimationFrame(requestId)
      requestId = undefined
    }
  }
  // translate the item as the mouse moves
  function render() {
    requestId = undefined
    if (firstRAFCycle) {
      // calculate position/sizes the first time
      calcBounds()
    }

    // calculate the mouse distance (current vs previous cycle)
    const mouseDistanceX = clamp(Math.abs(mousePosCache.x - mousepos.x), 0, 100)
    // direction where the mouse is moving
    direction = {
      x: mousePosCache.x - mousepos.x,
      y: mousePosCache.y - mousepos.y
    }
    // updated cache values
    mousePosCache = { x: mousepos.x, y: mousepos.y }

    // new translation values
    animatableProperties.tx.current =
      Math.abs(mousepos.x - bounds.el.left) - bounds.reveal.width / 2
    animatableProperties.ty.current =
      Math.abs(mousepos.y - bounds.el.top) - bounds.reveal.height / 2
    // new rotation value
    animatableProperties.rotation.current = firstRAFCycle
      ? 0
      : map(mouseDistanceX, 0, 200, 0, direction.x < 0 ? 60 : -60)
    // new filter value
    animatableProperties.brightness.current = firstRAFCycle ? 1 : map(mouseDistanceX, 0, 100, 1, 3)

    // set up the interpolated values
    // for the first cycle, both the interpolated values need to be the same so there's no "lerped" animation between the previous and current state
    animatableProperties.tx.previous = firstRAFCycle
      ? animatableProperties.tx.current
      : lerp(
          animatableProperties.tx.previous,
          animatableProperties.tx.current,
          animatableProperties.tx.amt
        )
    animatableProperties.ty.previous = firstRAFCycle
      ? animatableProperties.ty.current
      : lerp(
          animatableProperties.ty.previous,
          animatableProperties.ty.current,
          animatableProperties.ty.amt
        )
    animatableProperties.rotation.previous = firstRAFCycle
      ? animatableProperties.rotation.current
      : lerp(
          animatableProperties.rotation.previous,
          animatableProperties.rotation.current,
          animatableProperties.rotation.amt
        )
    animatableProperties.brightness.previous = firstRAFCycle
      ? animatableProperties.brightness.current
      : lerp(
          animatableProperties.brightness.previous,
          animatableProperties.brightness.current,
          animatableProperties.brightness.amt
        )

    // set styles
    gsap.set(DOM.reveal, {
      x: animatableProperties.tx.previous,
      y: animatableProperties.ty.previous,
      rotation: animatableProperties.rotation.previous,
      filter: `brightness(${animatableProperties.brightness.previous})`
    })

    // loop
    firstRAFCycle = false
    loopRender()
  }

  return {
    DOM
  }
}
